/*********************************
* Custom DM Admin Welcome Panel *
*********************************/
/* Welcome Panel */
.WelcomePanel {
  position: relative;
  overflow: auto;
  margin: 0;
  background: rgba(255, 255, 255, 0.368627451) url("../images/welcome-banner.jpg") no-repeat center !important;
  background-size: cover !important;
  background-position: 100%;
  background-blend-mode: overlay;
  font-size: 14px;
  line-height: 1.3;
}

.WelcomePanel__header {
  height: 15rem;
}

.WelcomePanel__header img {
  padding: 1rem;
}

.WelcomePanel__column-group {
  background: rgba(255, 255, 255, 0.7098039216);
  display: flex;
  justify-content: space-between;
  padding: 2rem;
}

.WelcomePanel__column {
  flex: 0 0 30%;
}

.WelcomePanel__column-content {
  padding-top: 1rem;
}

.welcome-panel-icon-styles,
.welcome-panel-icon-pages,
.welcome-panel-icon-layout {
  background-color: black;
}

.Dashboard__loomWidget {
  display: flex;
  justify-content: space-between;
}

#dmtheme_dashboard_widget {
  background: black;
  color: white;
}
#dmtheme_dashboard_widget .Dashboard__loomWidget-title,
#dmtheme_dashboard_widget .ui-sortable-handle {
  color: white;
}